<template>
  <div class="monster-collect">
    <div v-if="!isShowBig" class="collect-box">
       <img v-if="getLingCard === 'purpleLingCard'" @click="showBig('ling', 'purple')" class='collect-frame' src='@/assets/images/ling-card-purple.png' alt='frame'>
       <img v-else-if="getLingCard === 'greyLingCard'" @click="showBig('ling', 'grey')" class='collect-frame' src='@/assets/images/ling-card-grey.png' alt='frame'>
       <img v-else class='collect-frame' src='@/assets/images/small-frame.png' alt='frame'>
       <img class='collect-frame' src='@/assets/images/small-frame.png' alt='frame'>
       <img class='collect-frame' src='@/assets/images/small-frame.png' alt='frame'>
       <img class='collect-frame' src='@/assets/images/small-frame.png' alt='frame'>
    </div>
    <div v-else @click="hideBig" class="collect-big-box">
      <img v-if="src === 'purpleLing'" class='collect-big-photo' src='@/assets/images/ling-card-purple.png' alt='frame'>
      <img v-else-if="src === 'greyLing'" class='collect-big-photo' src='@/assets/images/ling-card-grey.png' alt='frame'>
      <div class="collect-head">{{ head }}</div>
      <div class="collect-text">{{ text }}</div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'MonsterCollect',
  components: {

  },
  data () {
    return {
      isShowBig: false,
      src: '',
      head: '',
      text: ''
    }
  },
  mounted () {

  },
  methods: {
    showBig (monster, color) {
      this.isShowBig = true
      if (monster === 'ling') {
        this.head = '林阿群'
        this.text = '林阿群從小生長在複雜的環境，家裡附近有很多彩虹與廢水。'
        if (color === 'purple') {
          this.src = 'purpleLing'
        } else {
          this.src = 'greyLing'
        }
      }
    },
    hideBig () {
      this.isShowBig = false
    }
  },
  computed: {
    getLingCard () {
      return this.$store.state.getLingCard
    }
  }
}
</script>
<style lang="scss" scoped>

  .monster-collect {
    min-height: 100vh;
    padding-top: 100px;
    background: linear-gradient(#000 70%, #95ff00);

    .collect {

      &-box {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
      }

      &-frame {
        width: 120px;
        height: 160px;
        margin: 20px;
      }

      &-big-box {
        max-width: 475px;
        width: 100%;
        position: fixed;
        top: 0px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-top: 120px;
        padding: 0px 50px 50px;
        color: #fff;
      }

      &-big-photo {
        width: 320px;
      }

      &-head {
        margin-top: 20px;
        font-size: 24px;
      }

      &-text {
        margin-top: 10px;
        padding: 0px 20px;
      }

    }
  }

</style>



